.documentsTitle{
    color: #262626;
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 33px;
}

.documentsInfo{
    max-width: 536px;
    color: #737373;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 45px;
}

.documentsAdvantages{
    color: #262626;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    margin-bottom: 25px;
}

.advantagesItem{
    display: flex;
    gap: 30px;
}

.advantageText{
    max-width: 637px;
    line-height: 24px;
    color: #262626;
}

.arrowImg{
    align-self: center;
}

.advantagesBox{
    margin-bottom: 43px;
}

.acceptPolis{
    border-radius: 6px;
    border: none;
    background: #CA0100;
    color: #fff;
    padding: 20px 55px;
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    cursor: pointer;
}

.documentsBox{
    display: flex;
    justify-content: space-between;
}

.documentsImg{
    margin-right: -60px;
}

@media screen and (min-width: 320px) and (max-width: 450px){
    .documentsTitle{
        font-size: 30px;
        margin-bottom: 15px;
    }
    .documentsBox{
        flex-direction: column;
        justify-content: normal;
    }
    .documentsImg{
        display: none;
    }
    .documentsInfo{
        font-size: 14px;
        margin-bottom: 16px;
    }
    .documentsAdvantages{
        font-size: 16px;
        margin-bottom: 15px;
    }
    .advantagesItem{
        margin-bottom: 15px;
    }
    .advantagesBox{
        margin-bottom: 30px;
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px){
    .documentsTitle{
        font-size: 40px;
        margin-bottom: 15px;
    }
    .documentsBox{
        flex-direction: column;
        justify-content: normal;
    }
    .documentsImg{
        display: none;
    }
    .documentsInfo{
        font-size: 20px;
        max-width: none;
        margin-bottom: 16px;
    }
    .documentsAdvantages{
        font-size: 25px;
        margin-bottom: 15px;
    }
    .advantagesItem{
        margin-bottom: 15px;
    }
    .advantageText{
        font-size: 20px;
    }
    .advantagesBox{
        margin-bottom: 30px;
    }
    .acceptPolis{
        display: block;
        margin: 0 auto;
    }
}